.Main ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.Main li {
  display: inline-block;
  margin: 10px 20px;
}
.Main a {
  color: black;
  text-decoration: none;
  display: block;
}
.Main a {
  width: 70px;
  height: 20px;
  display: block;
}

.Main header#nav {
  text-align: center;
  font-size: 1.2rem;
  font-weight: 600;
}
Button {
  margin-bottom: 20px;
}
.Main a:hover,
.Main a:active {
  color: #fa923f;
}
.Main {
  position: relative;
}
